.auth{
    width: 100%;
   
    background: rgb(7, 116, 122);
  
}
.lay{
    width: 100%;
    display: flex;
    justify-content: center;
    align-items: center;
    height:80%;
    padding: 3rem 0;
}
.form{
    width: 50%;
    height: 33rem;
    display: flex;
    flex-direction: column;
    justify-content: space-around;
    align-items: center;
    background: rgb(3, 56, 59);
    border-radius: 5px;
    
}
.auth_input{
    width: 70%;
    outline: none;
    height: 4rem;
    border: none;
    border-bottom: 1px solid #fff;
    border-radius: 6px;
    background: rgb(3, 56, 59);
    color:#fff;
    font-family: Georgia, 'Times New Roman', Times, serif;
}
.auth_input:focus{
    outline: none;
    border: none;
    border-bottom: 1px solid rgb(7, 116, 122);
}
.sineup{
    padding: .5rem 1rem;
    font-size: 2.3rem;
    border-radius: 6px;
    font-family: Georgia, 'Times New Roman', Times, serif;
    background: rgb(3, 56, 59);
    box-shadow: 0 .4rem .6rem #111;
    outline: none;
    color: rgb(187, 184, 184);
    border: 1px solid rgb(7, 116, 122);
}
.sineup:disabled{
    cursor: not-allowed;
    background: rgb(128, 125, 125);
    box-shadow: none;
}
.sineup:disabled:no-button
.sineup:hover{
    background: rgb(8, 114, 120)
}
.sineup:active{
    border: 1px solid rgb(4, 52, 54)
}
.valid{
    width: 70%;
    outline: none;
    height: 4rem;
    border: none;
    border-bottom: 1px solid #fff;
    border-radius: 6px;
    background: rgb(59, 3, 33);
    color:#fff;
    font-family: Georgia, 'Times New Roman', Times, serif;
}
.valid:focus{
    outline: none;
    border: none;
    border-bottom: 1px solid rgb(226, 24, 51);
}
@media (max-width:900px){
    
    .form{
        width:80%;
    }
}

.label_photo{
    background: #111;;
    color: white;
    font-family: Georgia, 'Times New Roman', Times, serif;
    font-size: 3vh;
    font-weight: 300;
    text-transform: capitalize;
    padding: .8rem 1.5rem;
    border-radius: 5px;
}
.label_photo:hover{
    background: rgb(51, 50, 50);
    transform: translateY(1px);
}
.label_photo:active{
    background: rgb(31, 30, 30);
    transform: translateY(0px);
}